html,head,body,div,ul,li,h1,hr{margin:0;padding:0;box-sizing:border-box}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:white}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}a,ul{text-decoration:none;color:wheat}

.App{text-align:center;background-color:#282c34;min-height:100vh}.App .app-menu{display:flex;flex-direction:row;align-items:center;justify-content:space-around;list-style:none}.App .app-menu a{width:100%;height:70px;display:flex;align-items:center;justify-content:center;color:white}.App .app-menu a:hover{background-color:skyblue}.App hr{border-color:slategray}

.Login{color:snow;height:80vh;display:flex;align-items:center;justify-content:center}.Login form{width:80%;display:flex;flex-direction:column;align-items:center}.Login form h1,.Login form label,.Login form input{margin-bottom:20px}.Login form input{height:30px;background-color:rgba(0,0,0,0);border-bottom:1px solid wheat;border-top:0;border-left:0;color:white;text-align:center}

.About{display:flex;flex-direction:column}.About h1{color:snow}

